import React from 'react';
import { NavLink, Navigate } from 'react-router-dom'; // Import Navigate for redirection
import classes from './MainNavigation.modules.css';

function MainNavigation() {
    return (
        <header className={classes.header}>
            <nav>
                <ul className={classes.list}>
                    <li>
                        <NavLink to="/" end>Home</NavLink>
                    </li>
                    <li>
                        <NavLink to="/Instructor/InstructorSignUp">Developers Sign Up</NavLink>
                    </li>
                    <li>
                        <NavLink to="/Instructor/InstructorSignIn">Developers Sign In</NavLink>
                    </li>
                    <li>
                        <NavLink to="/UserSignUp">New Customers Sign Up</NavLink>
                    </li>
                    <li>
                        <NavLink to="/Login">Customers Sign In</NavLink>
                    </li>
                </ul>
            </nav>
        </header>
    );
}

export default MainNavigation;
