import React, { useEffect } from 'react';
import './homestyles.css'; // Import the CSS file

function HomePage() {
    useEffect(() => {
        // Fetch a random image related to instructors, schools, books, libraries, or education from Unsplash API
        const fetchBackgroundImage = async () => {
            const accessKey = 'N5OA3mPRZ4QFUfIS90RvOsPUGe-_ebgML_VOjwpnLdsU';
            const apiUrl = `https://api.unsplash.com/photos/random?query=education&orientation=landscape&client_id=${accessKey}`;

            try {
                const response = await fetch(apiUrl);
                const data = await response.json();
                document.body.style.backgroundImage = `url(${data.urls.full})`;
                document.body.style.backgroundSize = 'cover';
                document.body.style.backgroundAttachment = 'fixed';
                document.body.style.backgroundColor = '#f0f0f0'; // Light background color
            } catch (error) {
                console.error('Error fetching background image:', error);
            }
        };

        fetchBackgroundImage();
    }, []);

    return (
        <div>
            <h1 className="lte-technology">LTE Technology</h1>
            <h2>WELCOME to Tech Solutions</h2>
        </div>
    );
}

export default HomePage;
