import React, { useState, useEffect } from 'react';

const InstructorSignUp = () => {
    const [user, setUser] = useState({
        Name: '',
        Email: '',
        CreatePassWord: '',
        ConfirmPassWord: '',
    });

    const data = (e) => {
        const name = e.target.name;
        const value = e.target.value;
        setUser({ ...user, [name]: value });
    };

    const isValidEmail = (email) => {
        // Regular expression for a simple email validation
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
        return emailRegex.test(email);
    };

    const getdata = async (e) => {
        e.preventDefault();

        const { Name, Email, CreatePassWord, ConfirmPassWord } = user;

        // Validate email format
        if (!isValidEmail(Email)) {
            alert('Invalid email address');
            return;
        }

        // Add validation to check if passwords match
        if (CreatePassWord !== ConfirmPassWord) {
            alert('Passwords do not match');
            return;
        }

        const options = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                Name,
                Email,
                CreatePassWord,
                ConfirmPassWord,
            }),
        };

        const res = await fetch(
            'https://jalalquiz-default-rtdb.firebaseio.com/UserData.json',
            options
        );

        if (res.ok) {
            alert('Message Sent');
        } else {
            alert('Error Occurred');
        }
    };

    useEffect(() => {
        // Fetch a random image related to instructors, teachers, professors, or trainers from Unsplash API
        const fetchBackgroundImage = async () => {
            const accessKey = 'Nq7qQtRgBh4yBsvBptL3WkddJiYHtI2FuTG2Ok1djKo';
            const apiUrl = `https://api.unsplash.com/photos/random?query=teacher&orientation=landscape&client_id=${accessKey}`;

            try {
                const response = await fetch(apiUrl);
                const data = await response.json();
                document.body.style.backgroundImage = `url(${data.urls.full})`;
                document.body.style.backgroundSize = 'cover';
                document.body.style.backgroundColor = '#f4f4f4'; // Light background color
            } catch (error) {
                console.error('Error fetching background image:', error);
            }
        };

        fetchBackgroundImage();
    }, []);

    return (
        <>
            <div className='form'>
                <div className='container'>
                    <form method='POST'>
                        <input
                            type='text'
                            name='Name'
                            placeholder='Your Name'
                            value={user.Name}
                            autoComplete='off'
                            required
                            onChange={data}
                        ></input>
                        <input
                            type='email'
                            name='Email'
                            placeholder='Email'
                            value={user.Email}
                            autoComplete='off'
                            required
                            onChange={data}
                        ></input>
                        <input
                            type='text'
                            name='CreatePassWord'
                            placeholder='Create Password'
                            value={user.CreatePassWord}
                            autoComplete='off'
                            required
                            onChange={data}
                        ></input>
                        <input
                            type='text'
                            name='ConfirmPassWord'
                            placeholder='Confirm Password'
                            value={user.ConfirmPassWord}
                            autoComplete='off'
                            required
                            onChange={data}
                        ></input>
                        <button onClick={getdata}>Submit</button>
                    </form>
                </div>
            </div>
        </>
    );
};

export default InstructorSignUp;
