import React, { useState, useEffect } from 'react';
import { signInWithEmailAndPassword } from './firebaseConfig';
import { useNavigate } from 'react-router-dom';
import { getAuth, onAuthStateChanged } from 'firebase/auth';

const InstructorSignIn = () => {
  const [credentials, setCredentials] = useState({
    emailOrUsername: '',
    password: '',
  });

  const [backgroundImage, setBackgroundImage] = useState('');
  const navigate = useNavigate();
  const auth = getAuth();

  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, (user) => {
      if (user) {
        navigate('/instructorDashboard');
      }
    });

    return () => unsubscribe();
  }, [auth, navigate]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCredentials((prevCredentials) => ({
      ...prevCredentials,
      [name]: value,
    }));
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    try {
      await signInWithEmailAndPassword(
        credentials.emailOrUsername,
        credentials.password
      );

      navigate('/instructorDashboard');
      console.log('Successfully signed in!');
    } catch (error) {
      console.error('Error signing in:', error.message);
    }
  };

  useEffect(() => {
    // Fetch a random image related to quiz, education, instructors, teachers from Unsplash API
    const fetchBackgroundImage = async () => {
      const accessKey = 'Nq7qQtRgBh4yBsvBptL3WkddJiYHtI2FuTG2Ok1djKo';
      const apiUrl = `https://api.unsplash.com/photos/random?query=education&orientation=landscape&client_id=${accessKey}`;

      try {
        const response = await fetch(apiUrl);
        const data = await response.json();
        setBackgroundImage(data.urls.full);
      } catch (error) {
        console.error('Error fetching background image:', error);
      }
    };

    fetchBackgroundImage();
  }, []);

  return (
    <div
      style={{
        backgroundImage: `url(${backgroundImage})`,
        backgroundSize: 'cover',
        minHeight: '100vh',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
      }}
    >
      <div>
        <h1>Developer Sign In</h1>
        <form onSubmit={handleSubmit}>
          <div>
            <label htmlFor="emailOrUsername">Email or Username:</label>
            <input
              type="text"
              id="emailOrUsername"
              name="emailOrUsername"
              value={credentials.emailOrUsername}
              onChange={handleChange}
              required
            />
          </div>
          <div>
            <label htmlFor="password">Password:</label>
            <input
              type="password"
              id="password"
              name="password"
              value={credentials.password}
              onChange={handleChange}
              required
            />
          </div>
          <button type="submit">Submit</button>
        </form>
      </div>
    </div>
  );
};

export default InstructorSignIn;
